.projectBlock {
    width: 400px;
    height: 400px;
    background-color: #252527;
    margin: 10px auto;
   
    
}

.icon {
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: center;    
    align-items: center;
    flex-direction: column;
   
    }

   .img {
    width: 100%;
    height: 100%;
    
   }

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #898b8d;
    color: white;
    text-decoration: none;
    width: 70px;
    height: 30px;
    margin: 0 auto;
    position: absolute;
  
    
}

.text {
    text-align: center;
    font-size: inherit;
    color: #fff;
    position: relative;
    
}

.title {
    font-size: 16px;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 300px;
    color: #ffffff;
    
}

.text::after {
    content: '';
    position: absolute;
    width: 90%;
    height: 5px;
    background-color: #4e93e6;
    transform: translate(-50%);
    
}



@media (min-width: 360px) and (max-width: 900px) {
   .projectBlock {
margin: 10px;
   }
   .projectBlock {
   height: 100%;
    
}
.text::after {
    position: relative;
    
}


}

