.header {
    width: 100%;
    height: 80px;
    background-color: #262626; 
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    z-index: 4;
    
}

.container {
    display: flex;
    justify-content: flex-end;
    
}

.a {
    display: flex;
    justify-content: space-between;
    width: 400px; 
}

.a a {
    color:#d2d2d2;
    text-decoration: none;
}

@media (min-width: 360px) and (max-width: 650px) {
  .container {
    justify-content: center;
  }
  .a a {
    margin-left: 15px;
  }
}