.projectBlock {
        background-color:#1f1f20;
        
}

.container {
      padding-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
}



.projects {
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
}

@media(min-width: 868px) and (max-width: 1666px) {
    .projects {
        justify-content: center;
    }
}