.mainBlock {
    height: 100vh;
    background-color:#1f1f20;
}

.container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        height: 100vh; 
}

.text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 550px;
    height: 100px;
}


.text a {
    color: #4e93e6;
    text-decoration: none;
    font-size: 13px;
}

.spans {
    text-decoration: underline;
    text-decoration-color:  #4e93e6;
    font-weight: normal;
}

.ramka {
    position: absolute;
    margin-left: 10px;
    width: 375px;
    height: 500px;
    border: 6px solid #4e93e6;
    z-index: 1;
   
}


.img {
    margin-top: 20px;
    z-index: 2;
    width: 350px;
    height: 450px;
}

.photo {
    display: flex;

    
    width: 350px;
    height: 450px;
    margin-right: 45px;
}

.p {
    color:  white;
}

.span {
    color: white;
}

.text h1 {
    color: white;
    font-size: 50px;
    letter-spacing: 5px;
    word-spacing: 10px;
}

@media (min-width:360px) and (max-width: 559px) {
   
    .text h1 {
        font-size: 48px;
    }
    .spans {
        font-size: 50px;
    }
    .text {
        margin-top: 78px;
        justify-content: center;
        text-align: center;
    }
    .img {
        width: 220px;
        height: 300px;
        
       }
       .ramka {
        width: 220px;
        height: 340px;
       } 

    
  
}



@media (min-width:560px) and (max-width: 649px) {
    .text {
        margin-top: 37px;
        text-align: center;
        justify-content: center;
    }

    .text h1 {
        font-size: 48px;
    }
   .container {
      justify-content: center;
   }
 
   .photo {
    margin-left: 45px;
}
.ramka {
    width: 355px;
    height: 500px;
   }
}

@media(min-width:650px) and (max-width: 925px) {
    .text {
        margin-top: 40px;
    }
}


@media (min-width: 650px ) and (max-width: 1726px) {
    .container {
        justify-content: center;
     }
    .text {
        text-align: center;
        justify-content: center;
    }

    .photo {
        margin-left: 45px;
    }
    .ramka {
        width: 355px;
        height: 500px;
       }

       
       
}