.contactsBlock {
    width: 100%;
    background-color: #1f1f20;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
   
   
}

.formContainer {
    
    margin-top: 30px;
    margin-bottom: 60px;
    width: 40%;
    background-color: aqua;
    display: flex;
    flex-direction: column;
}



.icon {
    width: 350px;
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 60px;

}

.telegram {
 width: 50px;
 height: 50px;
}




@media (min-width:360px) and (max-width:900px) {
    .formContainer {
        width: 90%;
    }
    
    
}




@media(min-width: 360px) and (max-width: 600px) {
    .telegram {
        width: 40px;
        height: 40px;
    }
    .icon {
        width: 180px;
        padding-bottom: 50px;
        padding-top: 30px;
    }
}


@media(min-width: 600px) and (max-width: 900px) {
    .telegram {
        width: 70px;
        height: 70px;
    }
    .icon {
        width: 230px;
        padding-bottom: 50px;
        padding-top: 30px;
    }
}


