.title::after {
    content: '';
    width: 55px;
    height: 1px;
    background-color: #4e93e6;
    position: absolute;
    bottom:5px;
    left: 50%;
    transform: translate(-50%);
}


.title h2 {
    
    font-family: 'Josefin Sans', sans-serif;
    font-size: 36px;
    font-weight: 300px;
    color: #fff;
    letter-spacing: 5px;
}

.title {
    margin-top: 30px;
     position: relative; 

}


@media(min-width: 360px) and (max-width: 430px) {
   .title h2 {
    text-align: center;
   }
}
