.skilsBlock {
    
    max-height: auto;
    background-color:#252527;
}

.title {
    background-color:#ffffff;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 36px;
    font-weight: 300px;
    color: #1f1f20;
    letter-spacing: 5px;
}

.skils {
   margin-top: 30px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    color: rgb(12, 12, 12);
 }



 .container {
  
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
 }

 @media(min-width: 520px) and (max-width: 1032px) {
    .skils {
      justify-content: center;
    }
 }

 @media(min-width: 1032px) and (max-width: 1549px) {
   .skils {
     justify-content: center;
   }
}